import React from 'react';
import Lesson from './Lesson';
import kept from '../../src/kept.png';
import murabito from '../../src/murabito.png';
import sizue from '../../src/sizue.png';


class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {murabitototalCount: 0,
                  sizuetotalCount: 0,
                  murabitowinCount: 0,
                  sizuewinCount:0};
  }
  
  murabitowinClick(){
    this.setState({murabitototalCount:this.state.murabitototalCount +1,
    murabitowinCount:this.state.murabitowinCount +1});
  }

  sizuewinClick(){
    this.setState({sizuetotalCount:this.state.sizuetotalCount +1,
    sizuewinCount:this.state.sizuewinCount +1});
  }

  murabitoloseClick(){
    this.setState({murabitototalCount:this.state.murabitototalCount +1});
  }

  sizueloseClick(){
    this.setState({sizuetotalCount:this.state.sizuetotalCount +1});
  }
  
  

  render() {
    let murabitoWin,sizueWin,Total,murabitoBtn,sizueBtn,random;
    
    
    if(this.state.murabitototalCount===0 && this.state.sizuetotalCount===0){
      Total=murabitoWin=sizueWin=(
        <p className='counter'>let's fight!!</p>
        );
      }else{
        Total=(
          <p className='counter'>
          {Math.floor((this.state.murabitowinCount + this.state.sizuewinCount)/(this.state.murabitototalCount+this.state.sizuetotalCount)*100)}%
        </p>
        );
        murabitoWin=(
          <p className='counter'>
          {Math.floor((this.state.murabitowinCount )/this.state.murabitototalCount*100)}%
        </p>
        );
        sizueWin=(
          <p className='counter'>
          {Math.floor((this.state.sizuewinCount)/this.state.sizuetotalCount*100)}%
        </p>
        );
      }
      
      if(this.state.murabitototalCount===0){
        murabitoWin=(
          <p className='warming'>No used ...</p>
          );
        }
        if(this.state.sizuetotalCount===0){
        sizueWin=(
          <p className='warming'>No used ...</p>
          );
        }
        
        
        
        const lessonList = [
          {
            name: 'murabito',
            image: murabito,
            introduction: murabitoWin,
            total:this.state.murabitototalCount,
            win:this.state.murabitowinCount
          },
          {
            name: 'total',
            image: kept,
            introduction: Total,
            total:this.state.murabitototalCount+this.state.sizuetotalCount,
            win:this.state.murabitowinCount+this.state.sizuewinCount
          },
          {
            name: 'shizue',
            image: sizue,
            introduction: sizueWin,
            total:this.state.sizuetotalCount,
            win:this.state.sizuewinCount
          },
        ];

        murabitoBtn = (
            <div className='lesson-card2'>
            <div className='lesson-item2'>
              <p>
                {this.state.murabitototalCount + this.state.sizuetotalCount+1} round
              </p>
            <img src={murabito} alt='a'></img>
          <button className='btn win' onClick={()=>{this.murabitowinClick()}}>win</button>
          <button className='btn lose' onClick={()=>{this.murabitoloseClick()}}>lose</button>
          </div>
          </div>
        );

        sizueBtn = (
        <div className='lesson-card2'>
        <div className='lesson-item2'>
              <p>
                {this.state.murabitototalCount + this.state.sizuetotalCount +1} round
              </p>
              <img src={sizue} alt='a'></img>
        <button className='btn win' onClick={()=>{this.sizuewinClick()}}>win</button>
        <button className='btn lose' onClick={()=>{this.sizueloseClick()}}>lose</button>
        </div>
        </div>
        );

        if(Math.random()>=0.5){
            random = murabitoBtn;
        }else{
            random = sizueBtn;
        }

return (
  <div className ='main'>
    {random}
        <h3>Win Rate</h3>
        {lessonList.map((lessonItem) => {
          return (
            <Lesson
            name={lessonItem.name}
            image={lessonItem.image}
            winRate={lessonItem.introduction}
            total={lessonItem.total}
            win={lessonItem.win}
            />
              );
            })}
        </div>
    );
  }
}

export default App;
